class FollowToggle{

    constructor(el){
        this.$el = $(el);
        this.userId = this.$el.data('user-id');
        this.followState = this.$el.data('inital-follow-state');
        this.$el.text(this.render())
    }

    render(){
        if (this.followState === 'unfollowed'){
            return 'Follow!'
        }else{
            return 'Unfollow!'
        }
    }
}

module.exports = FollowToggle;